export abstract class BasicOperations {
  protected numberArray: number[][];

  constructor() {
    this.numberArray = [];
  }

  public run() {
    this.numberArray;

    this.initNumbers();

    this.afterInitialisation();

    this.evaluateNumbers();

    this.afterEvaluation();
  }

  public evaluateNumbers() {
    if (this.numberArray.length === 0) {
      return 'El array no contiene ningÃºn nÃºmero';
    }

    return 'El array contiene elementos';
  }

  protected abstract initNumbers(): void;
  protected abstract reduce(array: number[]) : number;
  protected abstract map(array: number[]): (n : number) => number[];

  protected afterInitialisation() {}
  protected afterEvaluation() {}
  protected afterChildrenGeneration() {}
  protected afterSurvivorSelection() {}
}


export class AddMapReduce extends BasicOperations {
  constructor() {
    super();
  }

  protected initNumbers(): void {
    const firstArray: number[] = [1, 2, 3, 4, 5];

    this.numberArray.push(firstArray);
  }

  public map(array: number[]): (n: number) => number[] {
    return (sum) => {
      this.initNumbers();
      array = this.numberArray[0];

      const resultArray: number[] = [];

      array.forEach((element: number) => {
        resultArray.push(element + sum);
      });

      return resultArray;
    };
  }

  public reduce(array: number[]): number {
    this.initNumbers();
    array = this.numberArray[0];
    let sum: number = 0;

    for (let i = 0; i < array.length; i++) {
      sum += array[i];
    }

    return sum;
  }
}

export class SubMapReduce extends BasicOperations {
  constructor() {
    super();
  }

  protected initNumbers(): void {
    const firstArray: number[] = [1, 2, 3, 4, 5];

    this.numberArray.push(firstArray);
  }

  public map(array: number[]): (n: number) => number[] {
    return (sub) => {
      this.initNumbers();
      array = this.numberArray[0];

      const resultArray: number[] = [];

      array.forEach((element: number) => {
        resultArray.push(element - sub);
      });

      return resultArray;
    };
  }

  public reduce(array: number[]): number {
    this.initNumbers();
    array = this.numberArray[0];
    let sub: number = 0;

    for (let i = 0; i < array.length; i++) {
      sub -= array[i];
    }

    return sub;
  }
}

export class ProdMapReduce extends BasicOperations {
  constructor() {
    super();
  }

  protected initNumbers(): void {
    const firstArray: number[] = [1, 2, 3, 4, 5];

    this.numberArray.push(firstArray);
  }

  public map(array: number[]): (n: number) => number[] {
    return (mult) => {
      this.initNumbers();
      array = this.numberArray[0];

      const resultArray: number[] = [];

      array.forEach((element: number) => {
        resultArray.push(element*mult);
      });

      return resultArray;
    };
  }

  public reduce(array: number[]): number {
    this.initNumbers();
    array = this.numberArray[0];
    let mult: number = 1;

    for (let i = 0; i < array.length; i++) {
      mult *= array[i];
    }

    return mult;
  }
}

export class DivMapReduce extends BasicOperations {
  constructor() {
    super();
  }

  protected initNumbers(): void {
    const firstArray: number[] = [1, 2, 3, 4, 5];

    this.numberArray.push(firstArray);
  }

  public map(array: number[]): (n: number) => number[] {
    return (div) => {
      this.initNumbers();
      array = this.numberArray[0];

      const resultArray: number[] = [];

      array.forEach((element: number) => {
        resultArray.push(element/div);
      });

      return resultArray;
    };
  }

  public reduce(array: number[]): number {
    this.initNumbers();
    array = this.numberArray[0];
    let div: number = 1;

    for (let i = 0; i < array.length; i++) {
      div /= array[i];
    }

    return div;
  }
}

const prueba = new AddMapReduce();

console.log(prueba.map([])(2));
console.log(prueba.reduce([]));

const prueba2 = new SubMapReduce();

console.log(prueba2.map([])(2));
console.log(prueba2.reduce([]));

const prueba3 = new ProdMapReduce();

console.log(prueba3.map([])(2));
console.log(prueba3.reduce([]));

const prueba4 = new DivMapReduce();

console.log(prueba4.map([])(2));
console.log(prueba4.reduce([]));

console.log(prueba.evaluateNumbers());
